          </i>
        </div>
      </div>
      <!-- -end -->
      <!-- -start -->
      <div class="main">
        <!-- <div class="back"></div> -->
        <h2 class="hello">HELLO.</h2>
        <h2>Hi, It's <span>DYJ</span></h2>
        <h3 class="typing-text">I'm a <span class="multiple-text"></span></h3>
