<script>
import $ from "jquery";
import Typed from "typed.js";
import PictureWindow from "./PictureWindow.vue";

export default {
  name: "ZDYCard",
  components: {
    PictureWindow,
  },
  mounted() {
    document.onmousewheel = function () {
      // 
      if (event.wheelDelta > 0) {
        $(".shell .title h4").css({
          left: "50%",
          width: "560px",
          fontSize: "50px",
        });
        $(".shell").css({
          "background-color": "var(--card-color)",
          color: "var(--supple-font-color)",
        });
        $(".shell .slide .iconfont div").css({
          background: "var(--supple-font-color)",
        });
        $(".shell .slide .bottons .one").css({
          width: "18px",
          height: "18px",
          "background-color": "var(--card-color)",
          "box-shadow": "0 0 0 12px var(--supple-font-color)",
        });
        $(".shell .slide .bottons .two").css({
          width: "26px",
          height: "26px",
          "background-color": "var(--supple-font-color)",
          "box-shadow": "0 0 0 12px var(--card-color)",
        });
        $(".shell nav a").css({
          color: "var(--supple-font-color)",
        });
        $(".shell nav .ss").css({
          borderBottomColor: "var(--supple-font-color)",
        });
        $(".shell .main .circle").css({
          border: "19px solid var(--supple-font-color)",
          boxShadow:
            "0px 0px 30px 16px var(--shard-color) inset,0px 0px 30px 20px rgba(0, 0, 0, 0.25)",
        });
        $(".shell .main .banana").css({
          left: "-120px",
          width: "780px",
          height: "390px",
          top: "110px",
        });
        $(".shell .main h2,.shell .main h3").css({
          display: "block",
        });
        $(".shell .main .hello").css({
          display: "none",
        });
        $(".shell .content").css({
          display: "none",
        });
      }
      // 
      if (event.wheelDelta < 0) {
        $(".shell .title h4").css({
          left: "3%",
          width: "150px",
          fontSize: "35px",
        });
        $(".shell").css({
          "background-color": "var(--supple-font-color)",
          color: "var(--card-color)",
        });
        $(".shell .slide .iconfont div").css({
          background: "var(--card-color)",
        });
        $(".shell .slide .bottons .two").css({
          width: "18px",
          height: "18px",
          "background-color": "var(--supple-font-color)",
          "box-shadow": "0 0 0 12px var(--card-color)",
        });
        $(".shell .slide .bottons .one").css({
          width: "26px",
          height: "26px",
          "background-color": "var(--card-color)",
        });
        $(".shell nav a").css({
          color: "var(--card-color)",
        });
        $(".shell nav .ss").css({
          borderBottomColor: "var(--card-color)",
        });
        $(".shell .main .circle").css({
          border: "19px solid var(--card-color)",
          boxShadow:
            "0px 0px 30px 16px var(--shard-black-color) inset,0px 0px 30px 20px rgba(255, 255, 255, 0.25)",
        });
        $(".shell .main .banana").css({
          left: "610px",
          width: "200px",
          height: "100px",
          top: "420px",
        });
