          display: "none",
        });
        $(".shell .main .hello").css({
          display: "block",
        });
        $(".shell .content").css({
          display: "block",
        });
      }
    };

    var typed = new Typed(".multiple-text", {
      strings: [
        "Web Design",
        "Developer",
        "DevOps Engineer",
        "AI Hobbyist",
        "Data Analyst",
      ],
      typeSpeed: 100,
      backSpeed: 100,
      backDelay: 1000,
      loop: true,
    });
  },
  methods: {
    showPicture() {
      this.$refs.modalWindow.openModal();
    },
    handleClose() {
      // 
    },
  },
};
</script>

<style scoped>
@import url("@/assets/css/zdy/zdyCard.less");
